@use "@app/bancor-components/Container/BaseExternal";
@use "@app/bancor-components/Element/Form/Input";
@use "@app/bancor-components/Element/Form/InputSelect";
@use "@app/bancor-components/Style/Layout";
@use "@app/bancor-components/Style/Layout/Margin";
@use "@app/bancor-components/Style/Layout/Padding";
@use "@app/bancor-components/Style/Layout/Flex";
@use "@app/bancor-components/Element/Form/InputNumber";
@use "@app/bancor-components/Element/Form/InputCheckbox";
@use "@app/bancor-components/Element/Form/InputPassword";
@use "@app/bancor-components/Element/Button";
@use "@app/bancor-components/Element/Text";
@use "@app/bancor-components/Element/Image";
@use "@app/bancor-components/Style/Font";
@use "@app/bancor-components/Style/Color";
@use "@app/bancor-components/Style/Border";

$heigt-presentation-image: 286px;
$width-presentation-title: 251px;
$width-presentation-text: 251px;

.presentation {
    @include Flex.set($dir: column, $align: center, $justify: center);
    white-space: normal;
    @include Margin.set($bottom: zeta);

    .image {
        height: $heigt-presentation-image;
    }

    .containerText {
        @include Flex.set($dir: column, $align: center, $justify: center);
        @include Margin.set($top: gamma);

        .title {
            @include Font.set($family: heading, $size: delta, $weight: bold, $align: center);
            @include Color.set($fg: "white");
            width: $width-presentation-title;
        }

        .text {
            @include Margin.set($top: beta);
            @include Font.set($size: gamma, $align: center);
            @include Color.set($fg: "white");
            width: $width-presentation-text;
        }
    }

    .nextButton {
        @include Flex.set($justify: flex-end);
        @include Color.set($bg: "transparent");
        @include Font.set($weight: medium);
        @include Color.set($fg: "white");
        width: Layout.get-max-width(alpha);
    }

    .skipButton {
        @include Flex.set($justify: flex-start);
        @include Color.set($bg: "transparent");
        @include Font.set($weight: medium);
        @include Color.set($fg: "white");
    }

    .finishButton {
        @include Color.set($bg: "white");
        @include Font.set($weight: medium);
        @include Color.set($fg: "primary");
    }
}

$width-image-download-app: 144px;
$width-image-welcome: 244px;

#{BaseExternal.$selector} {
    .title#{Text.$selector} {
        @include Color.set($fg: "gray-dark");
        @include Font.set($size: delta, $weight: default, $family: "default", $align: default);
        @include Margin.set($bottom: gamma, $top: gamma);
    }

    .subtitle#{Text.$selector} {
        @include Color.set($fg: "gray-dark");
        @include Font.set($size: gamma, $weight: default, $family: "default", $align: default);
        @include Margin.set($bottom: delta);
    }

    .inputSelectContainer {

        #{Input.$selector},
        #{InputSelect.$selector},
        #{InputCheckbox.$selector},
        #{InputNumber.$selector},
        #{InputPassword.$selector} {
            @include Margin.set($bottom: delta, $top: delta);
        }

        #{InputCheckbox.$selector} label {
            @include Font.set($size: beta, $space: default);
        }
    }

    .submitButton#{Button.$selector} {
        @include Margin.set($bottom: delta);
    }

    .alert {
        @include Flex.set($align: center);

        &>#{Text.$selector} {
            @include Margin.set($left: beta);
        }

        .alertLottie {
            @include Flex.set($shrink: none);
        }
    }

    .termsAndConditions {
        @include Flex.set($justify: start);

        &>#{Text.$selector} {
            @include Color.set($fg: "gray-dark");
            @include Font.set($transform: caps);
        }

        #{Button.$selector} {
            @include Button.asUnderlineLink;
        }
    }

    #{Text.$selector}.validateTitle {
        @include Color.set($fg: "gray-dark");
        @include Font.set($size: delta, $weight: default, $family: "default", $align: default);
        @include Margin.set($top: gamma, $bottom: gamma);
    }

    #{Text.$selector}.emailValue {
        @include Color.set($fg: "gray-dark");
        @include Font.set($size: delta, $weight: bold, $family: "default", $align: default);
    }

    #{Text.$selector}.noteEmail {
        @include Font.set($size: beta, $weight: default, $family: "default", $align: default);
        @include Margin.set($top: beta, $bottom: beta);
    }

    #{Text.$selector}.subtitleEmail {
        @include Color.set($fg: "gray-dark");
        @include Font.set($size: gamma, $weight: default, $family: "default", $align: default);
        @include Margin.set($top: delta, $bottom: beta);
    }

    .inputContainerEmail {
        #{Input.$selector} {
            @include Margin.set($bottom: delta, $top: none);
        }
    }

    .resendCode {
        @include Flex.set($justify: center, $align: center);
        @include Margin.set($top: delta);

        #{Text.$selector} {
            @include Margin.set($right: alpha);
            @include Color.set($fg: "gray-dark");
            @include Font.set($size: gamma, $weight: default, $family: "default", $align: default);
        }

        #{Button.$selector} {
            @include Flex.set($justify: center);
            @include Color.set($bg: "transparent");
            @include Padding.set($preset: none);
            @include Border.set($style: default);

            #{Text.$selector} {
                @include Color.set($fg: "primary");
                @include Font.set($size: gamma,
                    $weight: medium,
                    $family: "default",
                    $align: default);
            }
        }
    }

    .inputContainerPhone {
        @include Margin.set($bottom: delta);

        #{Input.$selector} {
            @include Margin.set($bottom: beta, $top: beta);
        }
    }

    .securitySealContainer {
        @include Margin.set($bottom: gamma, $top: gamma);
    }

    .onboardingWelcome {
        @include Flex.set($dir: column, $justify: center, $align: center);
        @include Margin.set($bottom: delta);

        #{Image.$selector} {
            width: $width-image-welcome;
        }

        #{Text.$selector} {
            @include Color.set($fg: "gray-dark");
            @include Font.set($size: delta,
                $weight: bold,
                $family: "default",
                $align: default,
                $height: beta);
        }
    }

    .downloadPrincipal {
        @include Flex.set($dir: column, $justify: center, $align: center);
        @include Margin.set($bottom: delta);

        #{Image.$selector} {
            width: $width-image-download-app;
        }

        #{Text.$selector} {
            @include Color.set($fg: "gray-dark");
            @include Font.set($size: delta, $align: default);
            @include Margin.set($top: delta);
        }
    }

    .logosContainer {
        @include Flex.set($justify: space-between, $align: center);
    }
}
