@use "sass:string";

/// Return the selector for a component.
/// @param {string?} $name - The $name of your component.
/// @param {string?} $type - The $type of your component.
/// @return {string} The string needed to select your component.
@function selector($name: null, $type: null, $id: null) {
    @if $id {
        @return #{$id};
    } @else {
        $n: if($name, "[data-name=" + $name + "]", "[data-name]");
        $t: if($type, "[data-type=" + $type + "]", "[data-type]");

        @return $n + $t;
    }
}

$type-root: Root;
$type-container: Container;
$type-page: Page;
$type-section: Section;
$type-element: Element;
$type-provider: Provider;
$type-bundle: Bundle;

$selector-root: selector(null, $type-root);
$selector-container: selector(null, $type-container);
$selector-page: selector(null, $type-page);
$selector-section: selector(null, $type-section);
$selector-element: selector(null, $type-element);
$selector-bundle: selector(null, $type-bundle);

@function uuid($name) {
    @return #{$name}__#{string.unique-id()};
}
